/* アーカイブアイテム */
.article-archive__item {
  margin-top: 10px;
}

.article-archive__item:not(:first-child) {
  margin-top: 0.25rem;
}

/* 年のボタン */
.article-archive__item-btn {
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  color: #408F95;
  font-weight: 400;
}

/* トグルアイコン */
.article-archive__toggle-icon {
  width: 1.5rem;
  height: 1.5rem;
  background-image: url("../images/sankaku.svg");
  /* SVG画像のパスを指定 */
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.5rem;
  transition: transform 0.3s ease;
  /* 回転のアニメーション */
}

/* 月のリスト */
.article-archive__list {
  display: none;
  margin-left: 20px;
  padding-left: 0;
  list-style-type: none;
}

/* 月のアイテム */
.article-archive__list-item {
  list-style-image: url("../images/sankaku.svg");
  font-size: 1.5rem;
  color: #408F95;
  font-weight: 400;
  cursor: pointer;
  margin-top: 0.25rem;
  margin-left: 1.75rem;
  padding-left: 0.5rem;
}

.article-archive__link {
  position: relative;
}

.article-archive__link::after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 0.125rem;
  background: #408F95;
  bottom: 0;
  /*アンダーラインがaタグの下端から現れる*/
  transform: scale(0, 1);
  /*アンダーラインの縮尺比率。ホバー前はx方向に0*/
  transform-origin: center top;
  /*変形（アンダーラインの伸長）の原点がaタグ（各メニュー）の左端*/
  transition: transform 0.3s;
}

@media (any-hover: hover) {
  .article-archive__link:hover::after {
    transform: scale(1, 1);
  }
}
/* アニメーション付きでトグルアイコンが回転 */
.article-archive__item--open .article-archive__toggle-icon {
  transform: rotate(90deg);
}

.article-archive__item--open .article-archive__list {
  display: block;
}

