/* チェックボックス
---------------------------------------------*/
.contact-form__form-checkbox {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* labelでカスタムデザイン */
.contact-form__form-label {
  position: relative;
  padding-left: 1.25rem;
  /* チェックボックス分の余白 */
  cursor: pointer;
  line-height: 0.8;
  font-weight: 400;
  display: inline-block;
  user-select: none;
}

/* labelのbeforeでボックスを作る */
.contact-form__form-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0.875rem;
  height: 0.875rem;
  border: 0.0625rem solid #408F95;
  background: #fff;
  box-sizing: border-box;
  transition: border-color 0.2s;
}

.contact-form__form-label--red::before {
  border: 0.0625rem solid #C94800;
  background-color: rgba(201, 72, 0, 0.2);
}

/* チェック状態の時のデザイン */
.contact-form__form-checkbox:checked + .contact-form__form-label::after {
  content: "";
  position: absolute;
  top: -4px;
  left: 6px;
  transform: rotate(50deg);
  width: 8px;
  height: 16px;
  border-right: 2px solid #C94800;
  border-bottom: 2px solid #C94800;
}

.contact-form__checkbox-item {
  height: 1.75rem;
}

/* セレクトボックス
---------------------------------------------*/
.contact-form__form-select {
  appearance: none;
  width: 100%;
  /*セレクトボックスの横幅*/
  padding: 0.5rem 0 0.5rem 0.5rem;
  color: #333;
  font-weight: normal;
  /*テキストの色*/
  border: 1px solid #5cc0b5;
  /*枠線の太さ・形状・色*/
  border-radius: 3px;
  /*枠線の角丸*/
  background-color: #fff;
  /*背景色*/
  cursor: pointer;
  /*マウスカーソルをポインターに*/
  font-size: 0.75rem;
}
@media screen and (min-width: 768px) {
