    top: 1.25rem;
  }
}

/* Style for the text area
---------------------------------------------*/
.contact-form__textarea {
  width: 100%;
  /* Set the width */
  /* Set padding */
  border: 0.0625rem solid #408F95;
  /* Set border */
  border-radius: 4px;
  /* Optional: for rounded corners */
  font-size: 16px;
  /* Set font size */
  resize: vertical;
  /* Allow vertical resize */
  outline: none;
  /* Remove outline */
  box-sizing: border-box;
  /* Include border and padding in the element's dimensions */
}

