  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1.875rem;
  height: 0.125rem;
  background-color: #fff;
}

.gallery-modal__close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.gallery-modal__close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.gallery-modal__content {
  width: 100%;
