}

.hamburger span:nth-of-type(3) {
  bottom: 0;
  transition: 0.5s transform;
}

.hamburger.is-drawer-active span:nth-of-type(1) {
  transform: translateY(0.7rem) rotate(45deg);
}

.hamburger.is-drawer-active span:nth-of-type(2) {
  opacity: 0;
}

.hamburger.is-drawer-active span:nth-of-type(3) {
