/*========= ページトップのためのCSS ===============*/
/*リンクの形状*/
#page-top {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  border: solid 0.0625rem #408F95;
  border-radius: 50%;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  #page-top {
    width: 5rem;
    height: 5rem;
  }
}

@media (any-hover: hover) {
  #page-top:hover {
    background: #408F95;
    transition: background 0.3s;
  }
  #page-top:hover .return-top__image path {
    stroke: #fff;
  }
}
.return-top__image {
  transform: translateX(-25%);
  height: 80%;
  width: 20%;
}
@media screen and (min-width: 768px) {
  .return-top__image {
    width: 10%;
    height: 60%;
  }
}

/*リンクを右下に固定*/
#page-top {
  position: fixed;
  right: 1.25rem;
  z-index: 2;
  /*はじめは非表示*/
  opacity: 0;
  transform: translateY(100px);
}

/*　上に上がる動き　*/
#page-top.UpMove {
  animation: UpAnime 0.5s forwards;
}

@keyframes UpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/*　下に下がる動き　*/
#page-top.DownMove {
  animation: DownAnime 0.5s forwards;
}

@keyframes DownAnime {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 1;
    transform: translateY(100px);
  }
}
